.product{
    flex: 4;
    padding: 20px;
}
.productTitleContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.productAddButton{
    width: 80px;
    border: none;
    color: #fff;
    background-color: teal;
    border-radius: 5px;
    font-size: 16px;
    padding: 5px;
    cursor: pointer;
}

.productTop{
    display: flex;
}

.productTopLeft,.productTopRight{
    flex: 1;
}
.productTopRight{
    padding: 20px;
    margin: 20px;
    box-shadow: 0 0 15px -10px rgb(0, 0, 0,0.75);
}
.productInfoTop{
    display: flex;
    align-items: center;
}
.productInfoImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 20px;
}
.productName{
    margin-right: 5px;
    font-weight: 600;
}
.productInfoButtom{
    margin-top: 20px;
}
.productInfoItem{
    width: 180px;
    display: flex;
    justify-content: space-between;
}
.productInfoKey{
    font-weight: 600;
}
.productInfoValue{
    font-weight: 300;
}
.productBottom{
    padding: 20px;
    margin: 20px;
    box-shadow: 0 0 15px -10px rgb(0, 0, 0,0.75);
}
.productForm{
    display: flex;
    justify-content: space-between;
}
.productFormLeft{
    display: flex;
    flex-direction: column;
}
.productFormLeft > label {
    margin-bottom: 10px;
    color: gray;
}
.productFormLeft > input {
    border: none;
    padding: 5px;
    margin-bottom: 10px;
    border-bottom: 1px solid rgb(0, 0, 0);
}
.productFormLeft > input:focus{
    outline: none;
}
.productFormLeft > select {
    margin-bottom: 10px;
}
.productFormRight{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.productUploader{
    display: flex;
    align-items: center;
}
.productUploaderImg{
    width: 100px;
    height: 100px;
    border-radius: 10px;
    margin-right: 10px;
}
.productButton{
    border: none;
    padding: 5px;
    color: #fff;
    border-radius: 7px;
    font-weight: 600;
    cursor: pointer;
    background-color: rgb(12, 12, 192);
}
.productTitle{
    cursor: pointer;
}
.productTitle:hover{
    color: #1976d2;
}
.dark-mode .productTitle  {
    color: #ffffff;
}
.dark-mode .productTitle:hover {
    color: rgb(225, 237, 158);
}
.dark-mode .productName {
    color: #ffffff;
}
.dark-mode .productInfoKey{
    color: #ffffff;
}
.dark-mode .productInfoValue{
    color: #ffffff;
}
.dark-mode .productInput {
    background-color: rgb(19, 26, 36);
}

.dark-mode #inStock {
    color: #ffffff;
    background-color: rgb(19, 26, 36);
}
.dark-mode .option {
    color: rgb(255, 255, 255);
}
.dark-mode .textInInput {
    color: rgb(255, 255, 255);
}